import { Avatar } from '@material-ui/core'
import React, {useState} from 'react'
import './ChatScreen.css'

function ChatScreen() {
    const [input, setInput] = useState('')
    const [messages, setMessages] = useState([
        {
            name: 'Elena',
            message: 'ciao bellisimo come sta?ðŸ’–ðŸ’–',
            image: 'https://th.bing.com/th/id/OIP.oaSUbQHRfcwcGecL5nyTygHaJl?w=182&h=236&c=7&o=5&dpr=1.25&pid=1.7'
        },
        
    ])
    const handleSend = (e) => {
        e.prenventDefault()
        setMessages([...messages, {message : input }])
        setInput('');
    }

    return (
        <div className='chatScreen'>
            <p className='chatScreen__timestamp'>Ultima visita alle 23:59</p>
            {messages.map((message) => (
                message.name ? (
                 <div className='chatScreen__message'>
                    <Avatar 
                    className='chatScreen__image'
                    alt={message.name}
                    src={message.image}
                    />
                    <p className='chatScreen__text'>{message.message}</p>
                </div>

                ) : (
                    <div className='chatScreen__message'> 
                        <p className='chatScreen__textUser'>{message.message}</p>
                    </div>
                )
            ))}
            <div>
                <form className='chatScreen__form'>
                    <input 
                    value={input}
                    onChange={e => setInput(e.target.value)}
                    className='chatScreen__input' placeholder='type something' type='text'>
                    </input>
                    <button 
                    onClick={handleSend}
                    type='submit' className='input__button'>
                        SEND
                    </button>
                </form>
            </div>
        </div>
    )
}

export default ChatScreen
